'use client';

import { useState, useEffect, useCallback } from 'react';
import { ethers } from 'ethers';
import lotteryABIFile from '../../abis/Lottery.json';
import Link from 'next/link';
import { useSearchParams } from 'next/navigation';

// Extends the window object to use a browser wallet provider like MetaMask.
declare global {
  interface Window {
    ethereum?: any;
  }
}

const BNB_CHAIN_ID = '0x61';
const API_BASE_URL = 'http://localhost:3001';
const lotteryABI = lotteryABIFile.abi;

export default function AdminPage() {
  const [account, setAccount] = useState<string | null>(null);
  const [isCorrectNetwork, setIsCorrectNetwork] = useState(false);
  const [isProcessing, setIsProcessing] = useState(false);
  const [isOwner, setIsOwner] = useState(false); // Note: Now checks ownership of ANY room.
  const [error, setError] = useState<string | null>(null);
  const [successMessage, setSuccessMessage] = useState<string | null>(null);

  // This function now only checks if the connected account is the owner of ANY of the lottery contracts.
  // The backend will handle the true authorization.
  const checkIsOwner = useCallback(async () => {
    if (window.ethereum && isCorrectNetwork && account) {
        try {
            const roomsResponse = await fetch(`${API_BASE_URL}/rooms`);
            const rooms = await roomsResponse.json();
            if (!rooms || rooms.length === 0) {
                throw new Error("Could not fetch room configurations.");
            }
            
            const provider = new ethers.BrowserProvider(window.ethereum);
            // Check ownership of the first contract as a proxy for all of them
            // since the owner is the same for all contracts deployed by the script.
            const lotteryContract = new ethers.Contract(rooms[0].contractAddress, lotteryABI, provider);
            const ownerAddress = await lotteryContract.owner();
            setIsOwner(ownerAddress.toLowerCase() === account.toLowerCase());

        } catch (e) {
            console.error("Could not check owner status:", e);
            setIsOwner(false);
        }
    } else {
        setIsOwner(false);
    }
  }, [account, isCorrectNetwork]);

  const checkNetwork = useCallback(async () => {
    if (window.ethereum) {
      try {
        const chainId = await window.ethereum.request({ method: 'eth_chainId' });
        setIsCorrectNetwork(chainId === BNB_CHAIN_ID);
      } catch (error) {
        console.error("Failed to check network:", error);
        setIsCorrectNetwork(false);
      }
    }
  }, []);

  const connectWallet = async () => {
    if (typeof window.ethereum !== 'undefined') {
      try {
        const accounts = await window.ethereum.request({ method: 'eth_requestAccounts' });
        setAccount(accounts[0]);
      } catch (err: any) {
        console.error("Error connecting to MetaMask", err);
        setError("Error connecting to MetaMask.");
      }
    } else {
      setError('Please install MetaMask.');
    }
  };

  const handleStartDrawAll = async () => {
    setIsProcessing(true);
    setError(null);
    setSuccessMessage(null);
    try {
        const response = await fetch(`${API_BASE_URL}/start-draw-all`, { method: 'POST' });
        const data = await response.json();

        if (data.success) {
            setSuccessMessage(data.message || "Draw process for all rooms has been initiated.");
        } else {
            throw new Error(data.message || "An unknown error occurred on the server.");
        }
    } catch (err: any) {
        console.error("Failed to start draws:", err);
        setError(`Error: ${err.message}`);
    } finally {
        setIsProcessing(false);
    }
  };

  useEffect(() => {
    const handleAccountsChanged = (accounts: string[]) => {
      setAccount(accounts.length > 0 ? accounts[0] : null);
    };
    const handleChainChanged = () => checkNetwork();

    if (window.ethereum) {
      window.ethereum.on('accountsChanged', handleAccountsChanged);
      window.ethereum.on('chainChanged', handleChainChanged);
      (async () => {
        const accounts = await window.ethereum.request({ method: 'eth_accounts' });
        handleAccountsChanged(accounts);
        await checkNetwork();
      })();
      return () => {
        window.ethereum.removeListener('accountsChanged', handleAccountsChanged);
        window.ethereum.removeListener('chainChanged', handleChainChanged);
      };
    }
  }, [checkNetwork]);

  useEffect(() => {
    checkIsOwner();
  }, [account, isCorrectNetwork, checkIsOwner]);

  const renderContent = () => {
    if (!account) {
      return <button onClick={connectWallet} className="px-8 py-4 bg-blue-600 text-white font-bold text-2xl rounded-lg hover:bg-blue-500">Connect Wallet</button>;
    }
    if (!isCorrectNetwork) {
      return <p className="text-yellow-400">Please switch to BNB Testnet.</p>;
    }
    if (!isOwner) {
        return <p className="text-red-500">You are not the owner of the contracts.</p>;
    }
    return (
        <button
            onClick={handleStartDrawAll}
            disabled={isProcessing}
            className="px-8 py-4 bg-red-600 text-white font-bold text-2xl rounded-lg hover:bg-red-500 disabled:bg-gray-600"
        >
            {isProcessing ? 'Initiating Draws...' : 'Start Draw For All Rooms'}
        </button>
    );
  };

  return (
    <main className="flex min-h-screen flex-col items-center justify-center bg-gray-900 text-white p-4">
        {isProcessing && (
            <div className="absolute inset-0 bg-black bg-opacity-70 flex flex-col justify-center items-center z-50">
                <div className="w-16 h-16 border-4 border-yellow-400 border-t-transparent rounded-full animate-spin"></div>
                <p className="mt-4 text-white text-xl font-semibold">Processing Request...</p>
            </div>
        )}
        <div className="absolute top-5 left-5">
            <Link href="/" className="text-blue-400 hover:underline">‚Üê Back to Lobby</Link>
        </div>
        <div className="text-center">
            <h1 className="text-5xl font-bold mb-8">Global Admin Panel</h1>
            <div className="space-y-4">
                {renderContent()}
                {successMessage && <p className="text-green-400 mt-4 text-lg">{successMessage}</p>}
                {error && <p className="text-red-500 mt-4">{error}</p>}
            </div>
        </div>
    </main>
  );
} 